<template>
    <div id="map"></div>
    <!-- <div class="button-group">
        <button @click="changeSize(0)">Hide</button>
        <button @click="changeSize(400)">show</button>
        <button @click="displayMarker(markerPositions1)">marker set 1</button>
        <button @click="displayMarker(markerPositions2)">marker set 2</button>
        <button @click="displayMarker([])">marker set 3 (empty)</button>
        <button @click="displayInfoWindow">infowindow</button>
    </div>         -->
    <!-- {{ this.height }}
    {{ this.width }} -->
    <div class="bot">
        <b-button pill variant="primary" @click="displayOleum">오름</b-button>
        <b-button pill variant="primary" @click="displayOlle">제주올레</b-button>
        <b-button pill variant="primary" @click="displayHalla">한라산 둘레길</b-button>
        <b-button pill variant="primary" @click="displayHiking">등산로</b-button>
    </div>

</template>

<script>
import { toRaw } from "vue";
import {ref} from 'vue';
var apikey= ref('');

export default {
    name: "KakaoMap",
    props:{
        height:String,
        width:String,
        oleum_data:Object,
        olle_data:Object,
        halla_data:Object,
        hiking_data:Object,
    },

    data() {
        return {            
        };
    },

    created() {
        // console.log(process.env.VUE_APP_KAKAOMAP_API_KEY);
        apikey = process.env.VUE_APP_KAKAOMAP_API_KEY;
        // console.log('api_key ', apikey);
    },

    mounted() {
        if (! window.kakao || !window.kakao.maps) {            
            const script = document.createElement("script");
            script.onload = () => kakao.maps.load(this.initMap);
            script.src ="//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey="+apikey;
            document.head.appendChild(script);            
        } else {
            this.initMap();            
        }
    },

    methods: {        
        initMap() {
            const container = document.getElementById("map");
            
            container.style.width = `${this.width}`;
            container.style.height = `${this.height}`;
            
            const options = {
                center: new kakao.maps.LatLng(33.38, 126.58),
                level: 10,
            };
            this.map = new kakao.maps.Map(container, options);            
            this.map.relayout();
            
            // console.log(this.map);
            // toRaw(this.map).relayout();
        },

        displayOleum() {

            let features = this.oleum_data.features;
            features.forEach( element => {
                // console.log(element);
                // console.log(element.geometry.coordinates, element.properties.오름명);
                // let site = element.properties.오름명;

                let position = new kakao.maps.LatLng(element.geometry.coordinates[1], element.geometry.coordinates[0]);
                let dia = element.properties['반경']; 
                let name = element.properties['오름명']
                
                new kakao.maps.Circle({
                    map: toRaw(this.map),
                    center : position,
                    radius: dia,    
                    strokeColor: '#5347AA',
                    strokeOpacity: 0.5,
                    strokeWeight: 2,
                    fillColor: '#E51D1A',
                    fillOpacity: 0.3,
                    // 원의 중심좌표 입니다 
                    // radius: 5000, // 미터 단위의 원의 반지름입니다 
                    // strokeWeight: 5, // 선의 두께입니다 
                    // strokeColor: '#75B8FA', // 선의 색깔입니다
                    // strokeOpacity: 1, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
                    // strokeStyle: 'dashed', // 선의 스타일 입니다
                    // fillColor: '#CFE7FF', // 채우기 색깔입니다
                    // fillOpacity: 0.7  // 채우기 불투명도 입니다   
                });

                new kakao.maps.InfoWindow({
                    map: toRaw(this.map),
                    content: '<div style="width:120px;text-align:center;padding:2px;">오름명 : <b>'+ name +'</b></div>',
                    position: position,
                    removable: true,
                });                

            });
        },

        displayOlle() {

            let features = this.olle_data.features;
            
            features.forEach( element => {                
                let coord = element.geometry.coordinates;
                coord.forEach( points => {
                    var addpath=[];
                    points.forEach ( point => {
                        let position = new kakao.maps.LatLng(point[1], point[0]);
                        addpath.push(position);
                    });
                    new kakao.maps.Polyline({
                        map: toRaw(this.map),
                        path: addpath, // 선을 구성하는 좌표배열 입니다
                        strokeWeight: 3, // 선의 두께 입니다
                        strokeColor: '#0000FF', // 선의 색깔입니다
                        strokeOpacity: 0.7, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
                        strokeStyle: 'solid' // 선의 스타일입니다
                    });
                });
            });
        },

        displayHalla() {
            
            let features = this.halla_data.features;
            
            features.forEach( element => {                
                let coord = element.geometry.coordinates;
                coord.forEach( points => {
                    var addpath=[];
                    points.forEach ( point => {
                        let position = new kakao.maps.LatLng(point[1], point[0]);
                        addpath.push(position);
                    });
                    new kakao.maps.Polyline({
                        map: toRaw(this.map),
                        path: addpath, // 선을 구성하는 좌표배열 입니다
                        strokeWeight: 3, // 선의 두께 입니다
                        strokeColor: '#FF0000', // 선의 색깔입니다
                        strokeOpacity: 0.5, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
                        strokeStyle: 'solid' // 선의 스타일입니다
                    });
                });
            });

        },

        displayHiking() {

            let features = this.hiking_data.features;

            features.forEach( element => {                
                let coord = element.geometry.coordinates;
                coord.forEach( points => {
                    var addpath=[];
                    points.forEach ( point => {
                        let position = new kakao.maps.LatLng(point[1], point[0]);
                        addpath.push(position);
                    });
                    new kakao.maps.Polyline({
                        map: toRaw(this.map),
                        path: addpath, // 선을 구성하는 좌표배열 입니다
                        strokeWeight: 2, // 선의 두께 입니다
                        strokeColor: '#0000FF', // 선의 색깔입니다
                        strokeOpacity: 0.5, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
                        strokeStyle: 'solid' // 선의 스타일입니다
                    });
                });
            });


        },
        
    },
};
</script>

<style scoped>
.bot {
    margin-top: 10px;
}
